.header-nav__item {
  margin-right: 10px;
}

.header-nav__link {
  font-size: 16px;
}

.header-mobile {
  display: none;
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 999;
}

.home__title {
  font-size: 50px;
}

.home__subtitle {
  font-size: 25px;
}

.home__box {
  margin-top: 20px;
}

.home__link {
  display: block;
  margin: 10px auto;
}

.home__link:first-child {
  margin-right: auto;
}

.section__center {
  flex-direction: column;
  align-items: center;
}

.section__img {
  margin: 0 auto;
}

.section .products {
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}

.section .products__wrap {
  margin-top: 20px;
  margin-right: 0px;
}

.footer-social {
  text-align: center;
}
